.user_notify_outter_container {
    height: 1342px;

    background: #f6f6f6;
    .user_notify_inner_container {
        padding: 1em 5em;
        .notify_header {
            h2 {
                text-align: left;
                letter-spacing: 0px;
                color: #161616;
                font-size: 40px;
                line-height: 58px;
                font-weight: 600;
                font-family: "Montserrat", sans-serif;
            }
        }
        .notify_wrapper {
            .rwt__tab {
                padding: 1em 1em 0 0;
                margin: 0 3em 1em 0;
                text-align: center;
                letter-spacing: 0.9px;
                color: #161616;
                text-transform: uppercase;
                font-size: 18px;
                line-height: 22px;
                font-weight: 600;
                font-family: "Montserrat", sans-serif;
            }
            .rwt__tab[aria-selected="true"]:after {
                width: 80%;
                border-bottom: 3px solid #53bfd2;
            }
            .rwt__tablist:not([aria-orientation="vertical"]) {
                border-bottom: none;
            }
            .tabPanel_outter {
                .notify_box_container {
                    height: 800px;
                    overflow: auto;
                    .notify_box {
                        width: 95%;
                        height: 189px;

                        background: #ffffff;
                        box-shadow: 0px 3px 12px #16161608;
                        border-radius: 20px;

                        margin-top: 3em;

                        .closeIcon {
                            display: flex;
                            align-items: flex-start;
                            justify-content: flex-end;

                            img {
                                position: relative;
                                top: 20px;
                                right: 35px;
                                width: 13px;
                                height: 13px;
                            }
                        }
                        .notify_inner_box {
                            display: flex;
                            align-items: center;

                            padding: 2em;
                            margin-right: 1em;
                            .notify_profile {
                                img {
                                    position: relative;
                                    top: -15px;
                                    width: 82px;
                                    height: 82px;
                                }
                            }
                            .notify_message {
                                display: flex;
                                flex-direction: column;
                                margin-left: 2em;
                                h6 {
                                    text-align: left;
                                    letter-spacing: 0px;
                                    color: #2c2c2c;
                                    font-size: 20px;
                                    line-height: 32px;
                                    font-weight: normal;
                                    font-family: "Montserrat", sans-serif;
                                    span {
                                        text-align: left;
                                        text-decoration: underline;
                                        letter-spacing: 0px;
                                        color: #53bfd2;
                                        font-size: 20px;
                                        line-height: 32px;
                                        font-weight: normal;
                                        font-family: "Montserrat", sans-serif;
                                    }
                                }
                                .notify_time {
                                    display: flex;
                                    align-items: center;
                                    margin-top: 1em;
                                    img {
                                        width: 17px;
                                        height: 17px;
                                    }
                                    p {
                                        text-align: left;
                                        letter-spacing: 0px;
                                        font-size: 15px;
                                        font-weight: 500;
                                        font-family: "Montserrat", sans-serif;
                                        color: #898989;
                                        padding: 0;
                                        margin: 0 0 0 1em;
                                    }
                                }
                            }
                        }
                    }
                }
                button {
                    display: flex;
                    align-items: center;
                    text-align: right;
                    letter-spacing: 0.45px;
                    color: #161616;
                    text-transform: uppercase;
                    border: none;
                    margin: 2em 0;
                    font-size: 18px;
                    line-height: 22px;
                    font-weight: bold;
                    font-family: "Montserrat", sans-serif;
                }
            }
        }
    }
}
